@import url(https://fonts.googleapis.com/css2?family=Encode+Sans+Expanded:wght@300&display=swap);
/* You can add global styles to this file, and also import other style files */
:root
{
    --main-color:#0aad0a;
    --light-color:#f0f3f2;
    --shadow: rgba(145,158,171,.2) 0px 2px 4px -1px,rgba(145,158,171,.14) 0px 4px 5px 0px,rgba(145,158,171,.12) 0px 1px 10px 0px;
    --font-family:'Encode Sans Expanded', sans-serif;
    --rating-color:#ffc908;
}

body{
    font-family: 'Encode Sans Expanded', sans-serif !important;
    font-family: var(--font-family) !important;
}
.rating-color
{
    color: #ffc908;
    color: var(--rating-color);
}

.cursor-pointer
{
    cursor: pointer;
}
.network {
    padding: 20px;
    background-color: #fff;
    position: fixed;
    bottom: 30px;
    left:30px;
    z-index: 99999999;
    box-shadow: rgba(145,158,171,.2) 0px 2px 4px -1px,rgba(145,158,171,.14) 0px 4px 5px 0px,rgba(145,158,171,.12) 0px 1px 10px 0px;
    box-shadow: var(--shadow);
    width: -webkit-fit-content !important;
    width: -moz-fit-content !important;
    width: fit-content !important;
   

}
.brdr-main {
    border: solid 1px #0aad0a !important;
    border: solid 1px var(--main-color) !important;
}
a , a:hover {
    color: black !important;
    text-decoration: none !important;
}
.product:hover
{
    box-shadow: rgba(145,158,171,.2) 0px 2px 4px -1px,rgba(145,158,171,.14) 0px 4px 5px 0px,rgba(145,158,171,.12) 0px 1px 10px 0px;
    box-shadow: var(--shadow);
    border: solid 1px #0aad0a;
    border: solid 1px var(--main-color);
}
.product .btn 
{
    transition: opacity 1s , -webkit-transform 1s;
    transition: transform 1s , opacity 1s;
    transition: transform 1s , opacity 1s , -webkit-transform 1s;
    -webkit-transform: translateY(100%);
            transform: translateY(100%);
    opacity: 0;
}
.product:hover .btn 
{
    opacity: 1;
    -webkit-transform: translateY(0%);
            transform: translateY(0%);

}
.font-sm
{
    font-size: 14px;
}
.text-main
{
    color: #0aad0a !important;
    color: var(--main-color) !important;
}
.bg-main
{
    background-color: #0aad0a !important;
    background-color: var(--main-color) !important;
}
.bg-main-light 
{
    background-color: #f0f3f2 !important;
    background-color: var(--light-color) !important;
}
.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    -webkit-animation: App-logo-spin infinite 20s linear;
            animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

